import React, { useState, useEffect } from 'react';
import { toast } from 'react-hot-toast';
import Input from '@/components/form/input/InputField';
import Label from '@/components/form/Label';
import Button from '@/components/ui/Button';
import Select, { SelectOption } from '@/components/ui/Select';
import { Produto, produtoService } from '@/services/api/produto.service';
import { Fornecedor, fornecedorService } from '@/services/api/fornecedor.service';
import { CreateItemCotacaoDTO, ItemCotacao } from '@/services/api/item-cotacao.service';
import { formatCurrency } from '@/utils/format';

type FormErrors = {
  fornecedorId?: string;
  produtoId?: string;
  valorUnitario?: string;
  quantidade?: string;
};

type ItemCotacaoFormProps = {
  item?: ItemCotacao;
  cotacaoId: number;
  onSubmit: (data: CreateItemCotacaoDTO) => Promise<void>;
  onCancel: () => void;
};

const ItemCotacaoForm: React.FC<ItemCotacaoFormProps> = ({
  item,
  cotacaoId,
  onSubmit,
  onCancel
}) => {
  const [formData, setFormData] = useState<CreateItemCotacaoDTO>({
    fornecedorId: item?.fornecedorId || 0,
    produtoId: item?.produtoId || 0,
    cotacaoId,
    valorUnitario: item?.valorUnitario || '0',
    quantidade: item?.quantidade || 1,
    aprovada: item?.aprovada || false,
    vencedora: item?.vencedora || false
  });

  const [produtos, setProdutos] = useState<Produto[]>([]);
  const [fornecedores, setFornecedores] = useState<Fornecedor[]>([]);
  const [isLoadingData, setIsLoadingData] = useState(true);
  const [errors, setErrors] = useState<FormErrors>({});
  const [isSubmitting, setIsSubmitting] = useState(false);
  const [valorFormatado, setValorFormatado] = useState(
    item?.valorUnitario ? formatCurrency(Number(item.valorUnitario)).replace('R$', '').trim() : ''
  );

  useEffect(() => {
    loadProdutos();
    loadFornecedores();
  }, []);

  const loadProdutos = async () => {
    try {
      setIsLoadingData(true);
      const response = await produtoService.list({ page: 1, limit: 100 });
      setProdutos(response.data);
    } catch (error) {
      toast.error('Erro ao carregar produtos');
    } finally {
      setIsLoadingData(false);
    }
  };

  const loadFornecedores = async () => {
    try {
      setIsLoadingData(true);
      const response = await fornecedorService.list({ page: 1, perPage: 100 });
      setFornecedores(response.data);
    } catch (error) {
      toast.error('Erro ao carregar fornecedores');
    } finally {
      setIsLoadingData(false);
    }
  };

  const validateForm = () => {
    const newErrors: FormErrors = {};
    const required = ['fornecedorId', 'produtoId', 'valorUnitario', 'quantidade'];

    required.forEach(field => {
      if (!formData[field as keyof typeof formData]) {
        newErrors[field as keyof FormErrors] = `${field.charAt(0).toUpperCase() + field.slice(1)} é obrigatório`;
      }
    });

    if (formData.quantidade < 1) {
      newErrors.quantidade = 'Quantidade deve ser maior que zero';
    }

    if (Number(formData.valorUnitario) <= 0) {
      newErrors.valorUnitario = 'Valor unitário deve ser maior que zero';
    }

    setErrors(newErrors);
    return Object.keys(newErrors).length === 0;
  };

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    if (!validateForm()) return;

    setIsSubmitting(true);
    try {
      await onSubmit(formData);
    } catch (error) {
      toast.error('Erro ao salvar item da cotação');
    } finally {
      setIsSubmitting(false);
    }
  };

  const handleValorUnitarioChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const value = e.target.value.replace(/\D/g, '');
    const floatValue = parseInt(value) / 100;
    setValorFormatado(formatCurrency(floatValue).replace('R$', '').trim());
    setFormData({ ...formData, valorUnitario: floatValue.toFixed(2) });
  };

  return (
    <form onSubmit={handleSubmit} className="space-y-6">
      <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
        <div>
          <Label htmlFor="produtoId">
            Produto<span className="text-error-500">*</span>
          </Label>
          <Select
            id="produtoId"
            name="produtoId"
            value={
              produtos
                .filter(p => p.id === formData.produtoId)
                .map(p => ({ value: p.id.toString(), label: `${p.nome} (${p.sku})` }))[0]
            }
            options={produtos.map(p => ({
              value: p.id.toString(),
              label: `${p.nome} (${p.sku})`
            }))}
            onChange={(newValue) => {
              const option = newValue as SelectOption;
              if (option) {
                setFormData({ ...formData, produtoId: Number(option.value) });
              }
            }}
            isSearchable
            placeholder="Selecione um produto"
            error={errors.produtoId}
          />
        </div>

        <div>
          <Label htmlFor="fornecedorId">
            Fornecedor<span className="text-error-500">*</span>
          </Label>
          <Select
            id="fornecedorId"
            name="fornecedorId"
            value={fornecedores
              .filter(f => f.id === formData.fornecedorId)
              .map(f => ({ value: f.id.toString(), label: f.nome }))[0]
            }
            options={fornecedores.map(f => ({
              value: f.id.toString(),
              label: f.nome
            }))}
            onChange={(newValue) => {
              const option = newValue as SelectOption;
              if (option) {
                setFormData({ ...formData, fornecedorId: Number(option.value) });
              }
            }}
            isSearchable
            placeholder="Selecione um fornecedor"
            error={errors.fornecedorId}
          />
        </div>

        <div>
          <Label htmlFor="valorUnitario">
            Valor Unitário<span className="text-error-500">*</span>
          </Label>
          <Input
            id="valorUnitario"
            name="valorUnitario"
            value={valorFormatado}
            onChange={handleValorUnitarioChange}
            error={errors.valorUnitario}
            className="pl-8"
          />
        </div>

        <div>
          <Label htmlFor="quantidade">
            Quantidade<span className="text-error-500">*</span>
          </Label>
          <Input
            id="quantidade"
            name="quantidade"
            type="number"
            min="1"
            value={formData.quantidade}
            onChange={(e) => setFormData({ ...formData, quantidade: Number(e.target.value) })}
            error={errors.quantidade}
          />
        </div>

        <div className="col-span-2 flex gap-4">
          <div className="flex items-center">
            <input
              title="Item Aprovado"
              type="checkbox"
              id="aprovada"
              checked={formData.aprovada}
              onChange={(e) => setFormData({ ...formData, aprovada: e.target.checked })}
              className="w-4 h-4 text-primary-600 bg-gray-100 border-gray-300 rounded focus:ring-primary-500"
            />
            <Label htmlFor="aprovada" className="ml-2 mb-0">
              Item Aprovado
            </Label>
          </div>

          <div className="flex items-center">
            <input
              title="Proposta Vencedora"
              type="checkbox"
              id="vencedora"
              checked={formData.vencedora}
              onChange={(e) => setFormData({ ...formData, vencedora: e.target.checked })}
              className="w-4 h-4 text-primary-600 bg-gray-100 border-gray-300 rounded focus:ring-primary-500"
            />
            <Label htmlFor="vencedora" className="ml-2 mb-0">
              Proposta Vencedora
            </Label>
          </div>
        </div>
      </div>

      <div className="flex justify-end space-x-4 pt-4">
        <Button
          variant="secondary"
          onClick={onCancel}
          type="button"
        >
          Cancelar
        </Button>
        <Button
          variant="primary"
          type="submit"
          loading={isSubmitting}
        >
          {item ? 'Atualizar' : 'Adicionar'}
        </Button>
      </div>
    </form>
  );
};

export default ItemCotacaoForm;
