"use client";

import React, { useState, useEffect, use, ChangeEvent, useCallback } from 'react';
import { toast } from 'react-hot-toast';
import { Cotacao, cotacaoService } from '@/services/api/cotacao.service';
import { ItemCotacao, itemCotacaoService } from '@/services/api/item-cotacao.service';
import { Modal } from '@/components/ui/modal';
import ItemCotacaoForm from '@/components/cotacao/ItemCotacaoForm';
import Button from '@/components/ui/Button';
import { formatDate, formatCurrency } from '@/utils/format';
import ButtonCard from '@/components/ui/button/ButtonCard';
import Select, { SelectOption } from '@/components/ui/Select';
import Label from '@/components/form/Label';
import Pagination from '@/components/ui/Pagination';
import { ChampioIcon, CheckLineIcon, ChevronLeftIcon, CloseLineIcon } from '@/icons';
import Input from '@/components/form/input/InputField';

type FilterOptions = {
  aprovados?: boolean;
  vencedores?: boolean;
  orderBy?: 'preco_asc' | 'preco_desc' | 'quantidade_asc' | 'quantidade_desc';
  produtoIds?: number[];
  fornecedorIds?: number[];
};

export default function CotacaoDetalhesPage({ params }: { params: Promise<{ id: string }> }) {
  const { id } = use(params);
  const [cotacao, setCotacao] = useState<Cotacao | null>(null);
  const [items, setItems] = useState<ItemCotacao[]>([]);
  // const [filteredItems, setFilteredItems] = useState<ItemCotacao[]>([]);
  const [loading, setLoading] = useState(true);
  const [isModalOpen, setIsModalOpen] = useState(false);
  const [selectedItem, setSelectedItem] = useState<ItemCotacao | null>(null);
  const [products, setProducts] = useState<ItemCotacao['produto'][]>([]);
  const [fornecedores, setFornecedores] = useState<ItemCotacao['fornecedor'][]>([]);
  const [page, setPage] = useState(1);
  const [totalPages, setTotalPages] = useState(1);
  const [filters, setFilters] = useState<FilterOptions>({});
  const [limitPage, setLimitPage] = useState(9);



  const loadCotacao = useCallback(async () => {
    try {
      setLoading(true);
      const response = await cotacaoService.getById(Number(id));
      setCotacao(response.data);
    } catch (error) {
      toast.error('Erro ao carregar cotação');
    }
  }, [id]);

  const loadItems = useCallback(async () => {
    try {
      setLoading(true);
      const response = await itemCotacaoService.list({
        cotacaoId: Number(id),
        page,
        limit: limitPage,
        aprovados: filters.aprovados,
        vencedores: filters.vencedores,
        produtoIds: filters.produtoIds,
        fornecedorIds: filters.fornecedorIds,
        orderBy: filters.orderBy
      });
      setItems(response.data);
      setTotalPages(response.pagination.pages);

    } catch (error) {
      toast.error('Erro ao carregar itens');
    } finally {
      setLoading(false);
    }
  }, [page, filters, limitPage, id]);

  useEffect(() => {
    const itens = cotacao?.itens || [];
    setProducts(
      Array.from(
        new Map(
          itens
            .filter(item => item.produto)
            .map(item => [item.produto!.id, item.produto!])
        ).values()
      )
    );
    setFornecedores(
      Array.from(
        new Map(
          itens
            .filter(item => item.fornecedor)
            .map(item => [item.fornecedor!.id, item.fornecedor!])
        ).values()
      )
    );
    console.log('itens:', itens);
  }, [cotacao]);

  useEffect(() => {
    loadCotacao();
    loadItems();
  }, [loadCotacao, loadItems]);
  // useEffect(() => {
  //   console.log('cotacao changed:', JSON.stringify(cotacao, null, 2));
  // }, [cotacao]);

  // const applyFilters = () => {
  //   let result = [...items];
  //   console.log('Applying filters:', filters);

  //   // if (filters.aprovados) {
  //   //   result = result.filter(item => item.aprovada);
  //   // }

  //   // if (filters.vencedores) {
  //   //   result = result.filter(item => item.vencedora);
  //   // }

  //   // if (filters.produtoIds?.length) {
  //   //   result = result.filter(item => filters.produtoIds?.includes(item.produtoId));
  //   // }

  //   // if (filters.fornecedorIds?.length) {
  //   //   result = result.filter(item => filters.fornecedorIds?.includes(item.fornecedorId));
  //   // }

  //   // if (filters.orderBy) {
  //   //   result.sort((a, b) => {
  //   //     switch (filters.orderBy) {
  //   //       case 'preco_asc':
  //   //         return Number(a.valorUnitario) - Number(b.valorUnitario);
  //   //       case 'preco_desc':
  //   //         return Number(b.valorUnitario) - Number(a.valorUnitario);
  //   //       case 'quantidade_asc':
  //   //         return a.quantidade - b.quantidade;
  //   //       case 'quantidade_desc':
  //   //         return b.quantidade - a.quantidade;
  //   //       default:
  //   //         return 0;
  //   //     }
  //   //   });
  //   // }


  //   setFilteredItems(result);
  // };

  useEffect(() => {
    loadItems();
  }, [loadItems]);
  // useEffect(() => {
  //   applyFilters();
  // }, [items]);

  const handleSaveItem = async (data: any) => {
    try {
      if (selectedItem) {
        await itemCotacaoService.update(selectedItem.id, data);
        toast.success('Item atualizado com sucesso');
      } else {
        await itemCotacaoService.create({
          ...data,
          cotacaoId: Number(id)
        });
        toast.success('Item adicionado com sucesso');
      }
      setIsModalOpen(false);
      setSelectedItem(null);
      loadCotacao();
      loadItems();
    } catch (error) {
      toast.error('Erro ao salvar item');
    }
  };

  const handleDeleteItem = async (id: number) => {
    if (!window.confirm('Tem certeza que deseja excluir este item?')) return;

    try {
      await itemCotacaoService.delete(id);
      toast.success('Item excluído com sucesso');
      loadCotacao();
      loadItems();
    } catch (error) {
      toast.error('Erro ao excluir item');
    }
  };

  const handleToggleAprovado = async (item: ItemCotacao) => {
    try {
      await itemCotacaoService.update(item.id, {
        ...item,
        aprovada: !item.aprovada
      });
      toast.success(`Item ${!item.aprovada ? 'aprovado' : 'desaprovado'} com sucesso`);
      loadItems();
    } catch (error) {
      console.error('Erro ao atualizar item:', error);
      toast.error('Erro ao atualizar item');
    }
  };

  const handleToggleVencedor = async (item: ItemCotacao) => {
    try {
      await itemCotacaoService.update(item.id, {
        ...item,
        vencedora: !item.vencedora
      });
      toast.success(`Item ${!item.vencedora ? 'marcado' : 'desmarcado'} como vencedor`);
      loadItems();
    } catch (error) {
      toast.error('Erro ao atualizar item');
    }
  };
  const backCotacoes = () => {
    window.history.back();
  }

  if (!cotacao) return null;

  return (
    <div className="p-4 sm:p-6 lg:p-8">
      <div className="mb-8">
        <div className="flex flex-col sm:flex-row justify-between items-start sm:items-center gap-4">
          <div>
            <Button
              variant="secondary"
              size="xs"
              onClick={backCotacoes}
              className="mb-2 bg-transparent"
            >
              <ChevronLeftIcon className="w-5 h-5" />
              Voltar
            </Button>
            <h1 className="text-2xl font-semibold text-gray-900 dark:text-white">
              Cotação #{cotacao.id} - {cotacao.nome}
            </h1>
            <p className="text-sm text-gray-500 mt-1">
              Descrição - {cotacao.descricao}
            </p>
            <p className="text-sm text-gray-500 mt-1">
              Data de Abertura: {formatDate(cotacao.dataAbertura)} |
              Data de Fechamento: {formatDate(cotacao.dataFechamento)}
            </p>
          </div>
          <div className="flex items-center gap-2">
            <Button
              variant="primary"
              onClick={() => {
                setSelectedItem(null);
                setIsModalOpen(true);
              }}
            >
              Adicionar Item
            </Button>
          </div>
        </div>

        <div className="mt-6">
          <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-4 mb-6">
            <div>
              <Label>Status</Label>
              <Select
                isMulti
                options={[
                  { value: 'aprovados', label: 'Aprovados' },
                  { value: 'vencedores', label: 'Vencedores' }
                ]}
                onChange={(newValue: any) => {
                  const options = newValue as SelectOption[];
                  setFilters(prev => ({
                    ...prev,
                    aprovados: options?.some(v => v.value === 'aprovados'),
                    vencedores: options?.some(v => v.value === 'vencedores')
                  }));
                }}
                placeholder="Filtrar por status"
              />
            </div>

            <div>
              <Label>Ordenar por</Label>
              <Select
                options={[
                  { value: 'preco_asc', label: 'Menor Preço' },
                  { value: 'preco_desc', label: 'Maior Preço' },
                  { value: 'quantidade_asc', label: 'Menor Quantidade' },
                  { value: 'quantidade_desc', label: 'Maior Quantidade' }
                ]}
                onChange={(option) => {
                  const value = (option as SelectOption)?.value as FilterOptions['orderBy'];
                  setFilters(prev => ({ ...prev, orderBy: value }));
                }}
                placeholder="Ordenar por"
                isClearable
              />
            </div>

            <div>
              <Label>Produtos</Label>
              <Select
                isMulti
                options={products.map(produto => ({
                  value: String(produto?.id),
                  label: `${produto?.nome} (${produto?.sku})`
                } as SelectOption))}
                onChange={(newValue: any) => {
                  const options = newValue as SelectOption[];
                  const values = options?.map(o => Number(o.value)) || [];
                  setFilters(prev => ({ ...prev, produtoIds: values }));
                }}
                placeholder="Filtrar por produto"
              />
            </div>

            <div>
              <Label>Fornecedores</Label>
              <Select
                isMulti
                options={fornecedores.map(fornecedor => ({
                  value: String(fornecedor?.id),
                  label: fornecedor?.nome
                } as SelectOption))}
                onChange={(newValue: any) => {
                  const options = newValue as SelectOption[];
                  const values = options?.map(o => Number(o.value)) || [];
                  setFilters(prev => ({ ...prev, fornecedorIds: values }));
                }}
                placeholder="Filtrar por fornecedor"
              />
            </div>
          </div>

          <div className="flex justify-between mb-4 gap-2 flex-wrap">

            <div className='flex items-center gap-0.5'>
              <Label className='flex items-center mb-0 h-full'>Quantidade por pagina</Label>
              <Input
                className='max-w-24'
                defaultValue={limitPage}
                max={items.length}
                min={1}
                onChange={(e: ChangeEvent<HTMLInputElement>) => setLimitPage(Number(e.target.value) | 0)}
              />
            </div>
            <Pagination
              currentPage={page}
              totalPages={totalPages}
              onPageChange={setPage}
            />
          </div>
        </div>
      </div>

      {loading ? (
        <div className="grid gap-4">
          {[1, 2, 3].map((n) => (
            <div key={n} className="animate-pulse bg-gray-100 dark:bg-gray-800 rounded-lg h-24" />
          ))}
        </div>
      ) : (
        <div className="grid gap-4 grid-cols-1 md:grid-cols-2 lg:grid-cols-3">
          {items.length > 0 ? (
            items.map((item: ItemCotacao) => (
              <div
                key={item.id}
                // className="bg-white dark:bg-gray-800 rounded-lg shadow-sm border border-gray-200 dark:border-gray-700 p-4">
                className="bg-white dark:bg-gray-800 rounded-lg shadow-sm hover:shadow-md shadow-gray-200 dark:shadow-gray-700 border border-gray-200 dark:border-gray-700 p-4">
                <div className="flex justify-between items-start flex-wrap">
                  <div>
                    <h3 className="font-medium text-gray-900 dark:text-white">
                      {item.produto?.nome} ({item.produto?.sku})
                    </h3>
                    <p className="text-sm text-gray-500">
                      Fornecedor: {item.fornecedor?.nome}
                    </p>
                    <p className="text-sm text-gray-500">
                      Valor Unitário: {formatCurrency(Number(item.valorUnitario))} |
                      Quantidade: {item.quantidade}
                    </p>
                  </div>
                  <div className="flex gap-1 flex-wrap">
                    <Button
                      variant={item.aprovada ? "success" : "secondary"}
                      size="xs"
                      onClick={() => handleToggleAprovado(item)}
                      title={item.aprovada ? 'Desaprovar Item' : 'Aprovar Item'}
                    >
                      <CheckLineIcon className="w-4 h-4 overflow-hidden" />
                      {/* {item.aprovada ? 'Aprovado' : 'Aprovar'} */}
                    </Button>
                    <Button
                      variant={item.vencedora ? "success" : "secondary"}
                      size="xs"
                      onClick={() => handleToggleVencedor(item)}
                      title={item.vencedora ? 'Desmarcar Vencedor' : 'Marcar Vencedor'}
                    >
                      <ChampioIcon className="w-4 h-4 overflow-hidden" />
                      {/* {item.vencedora ? 'Vencedor' : 'Marcar Vencedor'} */}
                    </Button>
                    <ButtonCard
                      variant="secondary"
                      size="xs"
                      onClick={() => {
                        setSelectedItem(item);
                        setIsModalOpen(true);
                      }}
                      title="Editar Item"
                      icon="edit"
                    />
                    <ButtonCard
                      title="Excluir Item"
                      icon="delete"
                      variant="secondary"
                      size="xs"
                      onClick={() => handleDeleteItem(item.id)}
                    />
                  </div>
                </div>
              </div>
            ))
          ) : (
            <div className="text-center py-12">
              <p className="text-gray-500 dark:text-gray-400">
                Nenhum item encontrado nesta cotação.
              </p>
            </div>
          )}
        </div>
      )}

      <Modal
        isOpen={isModalOpen}
        onClose={() => {
          setIsModalOpen(false);
          setSelectedItem(null);
        }}
        className="max-w-[800px] m-4"
      >
        <div className="no-scrollbar relative w-full overflow-y-auto rounded-3xl bg-white p-4 dark:bg-gray-900 lg:p-11">
          <div className="px-2 pr-14">
            <h4 className="mb-2 text-2xl font-semibold text-gray-800 dark:text-white/90">
              {selectedItem ? 'Editar Item' : 'Novo Item'}
            </h4>
            <p className="mb-6 text-sm text-gray-500 dark:text-gray-400 lg:mb-7">
              {selectedItem 
                ? 'Atualize as informações do item.' 
                : 'Preencha os dados para criar um novo item.'
              }
            </p>
          </div>
          <ItemCotacaoForm
            item={selectedItem || undefined}
            cotacaoId={Number(id)}
            onSubmit={handleSaveItem}
            onCancel={() => {
              setIsModalOpen(false);
              setSelectedItem(null);
            }}
          />
        </div>
      </Modal>
    </div>
  );
}
